.nav-container{
    height: 70px;
    position: fixed;
    width: 100%;
    background-color: white;
    border-bottom: 1px solid #e9e9e9;
    z-index: 2;
}
.nav-container::after{
    content: "";
    display: block;
    position: absolute;
    bottom: 0;
    left: 0;
    right: 0;
    height: 1px;
    background-color: #e9e9e9;
}
.nav-backdrop{
    background-color: rgba(50, 50, 50, .95)   ;
    position: fixed;
    top: 0;
    bottom: 0;
    left: 0;
    right: 0;
    z-index: 1;
    visibility: hidden;
    opacity: 0;
    transition: all .4s ease-in-out;
}
.logo-link{
    position: relative;
    left: 1em;
    top: 16px;
}
.logo{
    height: 39px;
}
.header-navbar{
    position: relative;
    height: 100%;
    display: inline-block;
    margin-left: 113px;
}
.header-navbar-list{
    list-style: none;
    padding: 0;
    margin: 0 -1.75rem;
    height: 100%;
    display: flex;
    align-items: center;
}
.header-navbar-item{
    height: 100%;
}
.header-navbar-item-link{
    text-decoration: none;
    padding:0 1.75rem;
    height: 100%;
    font-size: 14px;
    font-weight: 600;
    color: #373737;
    position: relative;
    display: flex;
    align-items: center;
    justify-content: center;
    transition: color .2s ease-in-out;
}
.header-navbar-item-link::after{
    content: "";
    display: block;
    height: 4px;
    position: absolute;
    bottom: 0;
    left: 0;
    right: 0;
    background-color: #eb0a1e;
    opacity: 0;
    transition: opacity .4s ease-in-out;
}
.header-navbar-item-link:hover{
    color: #eb0a1e;
}
.header-navbar-item-link:hover:after{
    opacity: 3;
}

.active{
    color: #eb0a1e;
}
.active::after{
    content: "";
    display: block;
    height: 4px;
    position: absolute;
    bottom: 0;
    left: 0;
    right: 0;
    background-color: #eb0a1e;
    opacity: 1;
    transition: opacity .4s ease-in-out;
}

.header-navbar-item-link-text{
    font-size: 14px;
    font-weight: 600;
    font-stretch: normal;
    font-style: normal;
    line-height: normal;
    letter-spacing: normal;
}

.navbar-menu-container{
    display: inline-block;
    width: 80px;
    position: fixed;
    right: 1em;
    top: 23px;
}

.navbar-menu-buttom{
    position: relative;
    cursor: pointer;
    border: none;
    background: none;
}
.navbar-menu-text{
    color: #191919;
    font-family: Montserrat;
    font-size: 14px;
    margin-right: 4px;
}

.header-navbar-menu-hidden{
    text-align: right;
    border-left: 1px solid #e9e9e9;
    position: fixed;
    z-index: 2;
    right: 0;
    top: 0;
    bottom: 0;
    width: 100%;
    max-width: 400px;
    background-color: #fff;
    visibility: hidden;
    opacity: 0;
    transform: translateX(5%);
    transition: all .4s ease-in-out;
}
.open,.openB{
    visibility:visible;
    opacity: 1;
    transform: translateX(0);
    transition: all .4s ease-in-out;
}


.navbar-menu-container-list{
    position: absolute;
    bottom: 0;
    right: 0;
    left: 0;
    top: 70px;
    background: #efeeef;
    overflow-y: auto;
    overflow-x: hidden;

}
.navbar-menu-logo{
    margin-top: 1rem;
    margin-right: 2rem;
    margin-bottom: 2.5rem;
    margin-left: 2px;
}
.navbar-menu-button{
    position: relative;
    cursor: pointer;
    border: none;
    background: none;
}
.navbar-menu-button-text{
    color: #191919;
    font-family: Montserrat;
    font-size: 14px;
    margin-right: 6px;
}
.navbar-menu-list{
    position: relative;
    list-style: none;
    margin-bottom:3rem;
    padding: 1rem 0;
}
.navbar-menu-list::before{
    content: "";
    position: absolute;
    z-index: 0;
    top: 0;
    bottom: 0;
    left: -0.2rem;
    right: -1rem;
    background-color: white;
}
.navbar-menu-list-item{
    position: relative;
    z-index: 1;
    margin-top: 1rem;
    margin-right: 3rem;
    margin-bottom: 1rem;
}
.navbar-menu-list-item-link{
    text-decoration: none;
}
.navbar-menu-list-item-link-text{
    font-size: 20px;
    font-weight: normal;
    font-stretch: normal;
    font-style: normal;
    line-height: normal;
    letter-spacing: -0.1px;
    color: black;
}
.separador{
    position: relative;
    width: 94%;
    margin-top: 2em;
    margin-left: 3%;
    margin-bottom: 2em;
    border: solid 1px #e9e9e9;
}
.navbar-menu-list-second{
    position: relative;
    background: none;
    padding: 1rem 0;
}

@media only screen and (max-width: 750px) {
    .header-navbar{
        display: none;
    }
    .navbar-menu-container{
        width: 35px;
    }
    .navbar-menu-text{
        display: none;
    }
    .header-navbar-menu-hidden{
        max-width: 750px;
        min-width: 290px;
        top: 70px;
    }
    .open{
        max-width: 750px;
        min-width: 290px;
        top: 70px;
    }
    .openB{
        visibility:hidden;
        opacity: 0;
    }
  }